progress-circular>\n                </CButton>\n            </form>\n        </CCardBody>\n    </CCard>\n</template>\n<script>\nexport default {\n    name: \"DebtDetails\",\n    components: {},\n    data() {\n        return {\n            formData: {},\n            color: {\n                menu: false\n            },\n            errors: {},\n            updateLoading: false,\n            fetchLoading: false\n        };\n    },\n    mounted() {\n        this.fetch();\n    },\n    methods: {\n        fetch() {\n            let self = this;\n            if (self.fetchLoading) {\n                return;\n            }\n            self.fetchLoading = true;\n            let data = {\n                id: self.$route.params.id\n            };\n            this.$store\n                .dispatch(\"debt/details\", data)\n                .then(response => {\n                    self.formData = response.data.data;\n                    self.fetchLoading = false;\n                })\n                .catch(error => {\n                    self.fetchLoading = false;\n                });\n        },\n        update() {\n            let self = this;\n            if (self.updateLoading) {\n                return;\n            }\n            self.updateLoading = true;\n            let data = {\n                ...self.formData,\n                id: self.$route.params.id\n            };\n            self.loading = true;\n            this.$store\n                .dispatch(\"debt/update\", data)\n                .then(response => {\n                    self.formData = response.data.data;\n                    self.updateLoading = false;\n                })\n                .catch(error => {\n                    self.updateLoading = false;\n                });\n        }\n    }\n};\n</script>\n\n<style scoped>\n.v-text-field >>> input {\n    font-size: 0.8em;\n    font-weight: 100;\n}\n.v-text-field >>> label {\n    font-size: 0.8em;\n}\n.v-text-field >>> button {\n    font-size: 0.8em;\n